.chart-container{
	display: flex;
	flex-wrap: wrap;
}
.elements-wrapper{
	width: 20%;
	height: 40vh;
	margin-left: 5px;
	margin-right: 5px;
}
.chart{
	margin-top: 10px;
	width: 100%;
	height: 100%;
	border: 1px solid #ccc;
	box-shadow: 1px 2px 3px #ccc;
}
.chart-thumbnail-img{
	width: 50px;
	height: 50px;
}
.chart-menu-heading{
	font-size: 22px;
	margin-left: 10px;
}
select{
	height: 30px;
    margin-top: 10px;
    width: 120px;
    padding: 5px;
    font-size: 13px;
    outline: none;
    border: 1px solid #ccc;
    background: #f2f2f2;
}

.dropdown-menu{
	left: -100px;
}

#brandingLogo{
	display: none;
}

@media (max-width: 992px){
	.elements-wrapper{
		width: 95%;
		margin-left: 1.5rem;
	}
}

@media (max-width: 768px){
	.chart-container{
		flex-direction: column;
	}
	.elements-wrapper{
		width: 100%;
		margin-left: 0;
	}
}

